import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable } from "rxjs";
import { Analizar_XML } from './../../Backend/src_backend/index'
//import { Analizar_XML } from '../Backend/src_backend/index';
@Injectable({
  providedIn: 'root'
})
export class CompiladorService {
  constructor(private http: HttpClient) { }

  analizar(codigo: string) {
    console.log(codigo)



    Analizar_XML(codigo)
    var alv = {
      simbolo: Analizar_XML(codigo).simbolo,
      cst:Analizar_XML(codigo).cst
      
    }
    return alv
  }

  getAstBase64(ast: any): Observable<any> {
    const json = { ast: ast }
    return this.http.post<any>('http://localhost:3000/getAstBase64', json);
  }

}
